/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Christmas cards....","date":"11 Dec 1999","slug":"/1999-12-11-christmas-cards/","tags":"Y1999"},"html":"<p>For once we are preparing our Christmas cards early... here's Mama hard at work! (And, yes, that's a Tux Linux mascot behind her, even though she's strictly a Windows woman.)</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/fppaper.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"fppaper.jpg: She's checking her list...\" /></p>\n<p>It seems I've caught the attention of \"Scripting News\". Sorry, Dave, but when I put Curly in my template, Mama just had to play the wav file 30 times in a row. I hope she didn't break anything! (In preparation for the oncoming onslaught of visitors from Scripting News, I'm updating the <a href=\"http://shanson.editthispage.com/faq\">FAQ</a>.)</p>\n<p>Our only big plan for the weekend is to go see the new James Bond film, which just opened in Germany, on Sunday. If we hit the early show at the one English-language theatre in Hamburg, we should have no trouble getting a seat. (Our first date 19 years ago was a Bond film, so we've always had a soft spot for the guy.)</p>"},"next":{"fields":{"title":"Friday misc...","date":"10 Dec 1999","slug":"/1999-12-10-friday-misc/"}},"previous":{"fields":{"title":"Alsterhaus Christmas lights","date":"12 Dec 1999","slug":"/1999-12-12-alsterhaus-christmas-lights/"}}},{"node":{"fields":{"title":"Friday misc...","date":"10 Dec 1999","slug":"/1999-12-10-friday-misc/","tags":"Y1999"},"html":"<p>Still no word from VA Linux. A note on slashdot says to be patient. So I am. I tried calling, but the helpful folks couldn't find my name on any list.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/scottfon.jpg\" height=\"206\" width=\"300\" border=\"0\" alt=\"scottfon.jpg: Scott phoning home\" /></p>\n<p>Mama took a quiet day today. Her childbirth class had their final session here at our house last night. The 5 are all in Week 36 or better, and even the midwife is pregnant. </p>\n<p>Thanks to Userland for implementing the \"multiple days on the home page\" feature in Manila! That's a main feature of a squishdot site that can now be done in Manila by changing just one preference.</p>"},"next":{"fields":{"title":"Linux fever...","date":"09 Dec 1999","slug":"/1999-12-09-linux-fever/"}},"previous":{"fields":{"title":"Christmas cards....","date":"11 Dec 1999","slug":"/1999-12-11-christmas-cards/"}}},{"node":{"fields":{"title":"Linux fever...","date":"09 Dec 1999","slug":"/1999-12-09-linux-fever/","tags":"Y1999"},"html":"<p><a href=\"http://www.valinux.com\">VA Linux</a> opened NASDAQ trading today with a bang... but I don't know if I made it on the train or not.</p>\n<p>As a \"Debian\" developer, I received 'the letter', returned the paperwork, and received confirmation that my paperwork was received. However, German applicants this week received notice that due to problems with German securities regulations, German residents were not eligible to participate in the IPO.</p>\n<p>However, on my paperwork I indicated that I am an American citizen, and I never received that notice. In fact, I have heard nothing about my application other than the notice that it had been received. However, as soon as I heard the price was set at $30, I sent in a confirmation of my willingness to buy at this price.</p>\n<p>I called VA Linux this evening, and they could not find my name on any of their lists. But they are going to call me back.</p>\n<p>With Red Hat, I also didn't know until a couple of days later that I had actually received my shares. We'll see what happens this time.</p>\n<p><i>I just noticed that this site is No. 100 on the editthispage.com <a href=\"http://www.editthispage.com/SiteRankings\">site rankings</a>, out of over 300 sites. I find it somewhat hard to believe that it is more interesting that over half of the other sites! The only advertising I've done was an ad on the weblog monitor... only because I wanted to remove the <a href=\"http://www.mausnews.de\">Maus Newsletter</a>, but there's no option to remove an ad... only to change the URL and the graphic!</i></p>"},"next":{"fields":{"title":"Ready and Waiting...","date":"07 Dec 1999","slug":"/1999-12-07-ready-and-waiting/"}},"previous":{"fields":{"title":"Friday misc...","date":"10 Dec 1999","slug":"/1999-12-10-friday-misc/"}}}],"pathPrefix":"","first":false,"last":false,"index":1100,"pageCount":1101}}

/***/ })

